.container {
  display: grid;
  grid-template-columns: 33% 33% 33%; }

.container a {
  text-decoration: none; }

.collections, .other-links, .getting-started {
  border: 1px solid #dcdee0;
  border-right: 0; }

.collections {
  border-left: 0; }

.icon-collections, .icon-link {
  float: right;
  position: relative;
  top: -37px;
  font-size: 29px;
  fill: #99a1a7;
  margin-top: 10px;
  margin-right: 24px; }


.icon-paperplane {
  float: left;
  font-size: 29px;
  margin-right: 12px;
  margin-top: 10px;
  margin-left: 10px;
  fill: white; }


.getting-started__headline {
  background-color: #60686f; }


.getting-started__headline h3 {
  margin-top: 0px;
  padding-top: 10px;
  color: white; }

.getting-started__headline h6 {
  padding-bottom: 10px;
  color: white; }

.getting-started__hedlinebtn {
  float: right;
  position: relative;
  top: -51px;
  width: 86px;
  margin-right: 9px;
  color: white; }

.float:after {
  content: "";
  clear: both;
  display: table; }

.twitter {
  background-color: #edeff1;
  padding: 20px;
  font-size: 12px;
  padding-bottom: 0; }

.twitter__userpic {
  float: left;
  margin-right: 16px; }

.icon-speech {
  fill: #99a1a7;
  font-size: 30px; }

.twitter__username {
  @include default-font(18px); }

.twitter__username a {
  color: rgb(56, 62, 66); }

.twitter__user {
  @include default-font(13px);
  margin-bottom: 28px; }

.twitter__user a {
  color: rgb(10, 79, 139); }

.twitter__speechicon {
  float: right; }

.twitter__text {
  @include default-font(16px);
  color: rgb(96, 104, 111);
  line-height: 1.2; }

.twitter__date {
  @include default-font(13px);
  color: rgb(96, 104, 111);
  margin-top: 36px; }

.icon-twitter {
  font-size: 24px;
  position: relative;
  top: 7px;
  fill: #99a1a7; }

.twitter__arrows {
  width: 100%;
  text-align: right;
  position: relative;
  top: -20px; }

.twitter__arrowleft, .twitter__arrowright {
  background-color: white;
  display: inline-block;
  padding: 6px 6px 3px 6px;
  border-radius: 22px; }

.icon-share {
  float: right;
  font-size: 29px;
  fill: #99a1a7;
  margin-top: 10px;
  margin-right: 24px; }

