.header__container {
  display: grid;
  grid-template-columns: 286px auto auto;
  padding: 12px 18px 8px 24px; }


//.header__logo, .header__search, .header__account
//  border: 1px solid

.logo {
  @include default-font(37px);
  color: rgb(0, 2, 3);
  line-height: 1.2;
  text-align: left; }


.header__search {
  display: grid;
  grid-template-columns: auto auto auto; }

.header__searcharea {
  display: flex; }

.search {
  @include default-font(12px);
  border: 1px solid #dcdee0;
  color: rgb(46, 50, 54);
  line-height: 1.2;
  padding: 8px 1px 7px 12px;
  height: 32px;
  margin-top: 6px; }

.search__icon {
  margin-top: -9px;
  margin-left: -1px; }

.icon-search {
  border: 1px solid #dcdee0;
  height: 32px;
  width: 32px; }

.icon-search, .icon-advanced-settings {
  fill: #53595e;
  font-size: 22px; }

.search__settings {
  margin-top: -17px; }

.search__settings a {
  text-decoration: none;
  @include default-font(12px);
  color: rgb(10, 79, 139);
  line-height: 1.2; }


.header__account {
  display: grid;
  grid-template-columns: 45px auto auto; }

.icon-home {
  fill: #878c8f; }

.header__account .icon-home {
  background: #dcdee0;
  border-radius: 36px;
  padding: 10px;
  font-size: 35px;
  margin-top: 7px; }

.header__accountname {
  text-decoration: none;
  @include default-font(12px);
  color: rgb(46, 50, 54);
  margin-top: 17px; }

.header__nav {
  grid-column: 1 / 3; }

.header__nav a, .header__background-jobs a {
  text-decoration: none;
  text-transform: uppercase;
  @include default-font(12px);
  color: rgb(46, 50, 54);
  line-height: 4.167; }

.header__nav .is-active {
  background-color: #383e42;
  padding: 12px;
  color: white; }

.header__background-jobs {
  text-align: right; }

.icon-background-jobs {
  fill: rgb(46, 50, 54); }

.header__background-jobsnum {
  background-color: #ff5f4b;
  padding: 0px 7px 1px 6px;
  border-radius: 11px;
  color: white;
  margin-left: 15px; }
